import { Toast } from "mint-ui";
import axios from "axios";
let ajaxGet = function (url, data) {
  data.vv = 6;

  return axios.get(url, data).then(res => {
    if (res.data.resultCode == 0) {
      return res.data;
    } else {
      msg(res.data.resultMsg);
    }
  });
};
let ajaxPost = function (url, data) {
  return axios.post(url, data, { vv: 6 }).then(res => {
    if (res.data.resultCode == 0) {
      return res.data;
    } else {
      msg(res.data.resultMsg);
    }
  })
}
let msg = function (msg) {
  Toast({
    message: msg,
    position: "bottom",
    duration: 3000
  });
};

export default ajaxGet;
export { ajaxPost }



