<template>
  <div class="content-header">
    <div class="content-goback" @click="goback()" v-if="btnShow=='true'">
      <div class="iconfont icon-jiantouzuo goback-icon"></div>
      <span class="goback-text">返回</span>
    </div>
    <div class="title">{{titleText}}</div>
    <div class="content-right" @click="rightFun()">
      <span v-if="headRightObj&&headRightObj.showWhat=='kitchenInfo'" class="iconfont icon-beizhu"></span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    btnShow: String,
    titleText: String,
    url: String,
    isComponents: {
      type: Boolean,
      default: false
    },
    headRightObj: {
      showWhat: "",
      text: ""
    }
  },
  data() {
    return {};
  },
  methods: {
    goback() {
      if (this.isComponents) {
        this.$emit("emitGoback");
      } else {
        this.$router.push(this.url);
      }
    },
    rightFun() {
      this.$emit("emitHeadRight");
    }
  }
};
</script>
<style lang="less" scoped>
.content-header {
  height: 46px;
  width: 100%;
  background-color: #393c41;
  display: flex;
  align-items: center;
  color: #fff;
  font-size: 16px;
  position: fixed;
  top: 0px;
  z-index: 9;
  .content-goback {
    position: absolute;
    left: 0;
    top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    .goback-icon {
      margin-left: 13px;
    }
    .goback-text {
      font-size: 16px;
    }
  }
  .title {
    font-size: 18px;
    flex: 1;
    text-align: center;
  }
  .content-right {
    position: absolute;
    right: 15px;
    top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    .icon-beizhu {
      font-size: 18px;
    }
  }
}
</style>



