<template>
  <div class="content-selectClass">
    <headerUsually titleText="选择班别" btnShow="false" url></headerUsually>
    <div class="content-main">
      <div
        class="item"
        v-for=" (item,index) in classList"
        :key="index"
        @click="selectItem(index,item)"
      >
        <div class="text" :class="activeItem.code==index?'activeText':''">{{item.descript}}</div>
        <div class="iconfont icon-xuanzhong" v-if="activeItem.code==index"></div>
      </div>
    </div>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast, MessageBox } from "mint-ui";
import { mapState, mapGetters } from "vuex";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      classList: [],
      activeItem: {
        code: -1,
        text: ""
      }
    };
  },
  computed: {
    ...mapState({
      userInfos: state => state.mobileOrdering.userInfo
    })
  },
  async mounted() {
    let dcDishInfo = localStorage.getItem("dcDishInfo")
      ? JSON.parse(localStorage.getItem("dcDishInfo"))
      : "";

    // console.log("dcDishInfo:", dcDishInfo);
    if (dcDishInfo) {
      //MessageBox.confirm("是否更新菜单！")
      MessageBox.confirm("", {
        title: "提示",
        message: "是否更新菜单！",
        closeOnClickModal: false
      })
        .then(action => {
          this.getDishInfo();
        })
        .catch(error => {
          this.getClass();
        });
    } else {
      this.getDishInfo();
    }
  },
  methods: {
    getDishInfo() {
      this.$http
        .get("/pos/router?method=getCachePluDatas", {
          vv: 6,
          timeout: 3 * 60 * 1000 // 3分钟
        })
        .then(res => {
          if (res.data.resultCode == 404) {
            MessageBox.confirm("", {
              title: "提示",
              message: res.data.resultMsg,
              closeOnClickModal: false
            })
              .then(action => {
                this.getDishInfo();
              })
              .catch(action => {
                this.$router.push("/mobileOrdering/login");
              });
          } else if (res.data.resultCode == 0) {
            let dcDishInfo = res.data.data;
            this.$store.commit("submitDishInfo", res.data.data);
            localStorage.setItem("dcDishInfo", JSON.stringify(dcDishInfo));
            this.getClass();
          } else {
            this.msg(res.data.resultMsg);
          }
        });
    },
    async getClass() {
      let data = {
        params: {
          type: "pos_cashier"
        }
      };
      let res = await ajaxGet("/pos/router?method=getCodeBase", data);
      if (res && res.resultCode == 0) {
        this.classList = res.data;
      }
    },
    selectItem(index, item) {
      this.activeItem.code = index;
      let dcgateway = localStorage.getItem("dcgateway")
        ? JSON.parse(localStorage.getItem("dcgateway"))
        : {};
      dcgateway.userInfo.shift = item.code;
      dcgateway.userInfo.shiftDescript = item.descript;
      localStorage.setItem("dcgateway", JSON.stringify(dcgateway));
      this.$router.push("/mobileOrdering/index");
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less">
.content-selectClass {
  padding-top: 46px;
  background-color: #f6f8f9;
  height: 100vh;
  box-sizing: border-box;
  .content-main {
    background-color: #fff;
    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 15px;
      height: 50px;
      border-bottom: 1px solid #ddd;
      .text {
        font-size: 14px;
        color: #000000;
      }
      .activeText {
        color: #c4ad68;
      }
      .iconfont {
        color: #c4ad68;
      }
    }

    .item:last-child {
      border-bottom: none;
    }
  }
}
</style>



